.img-container {
  overflow: hidden;
  border-radius: 2px;
}

.hotel-img {
  width: 200px;
  height: 150px;
  object-fit: cover;
  transition: all 0.2s ease-in-out;
}

@media (max-width: 525px) {
  .hotel-img {
    width: 100px;
  }
}

.hotel-img:hover {
  transform: scale(1.1);
}

.hotel-item {
  width: 100%;
  max-width: 700px;
  margin: 2em 0 !important;
}
