.show-map {
  cursor: pointer;
  margin-left: 16px;
  text-decoration: underline;
  color: #002766;
}

@media (max-width: 576px) {
  .show-map {
    display: none;
  }
}

.map-container {
  position: relative;
}

.map-spin {
  position: absolute !important;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
