.average-rating-container {
  --length: 5em;
  background-color: #002766;
  color: white;
  width: var(--length);
  height: var(--length);
  text-align: center;
  line-height: var(--length);
  border-radius: 6px 6px 6px 0;
}

.average-rating-container .rating {
  font-weight: bold;
  font-size: 2.3em;
}

.average-rating-container .small {
  font-size: 0.5rem;
}

@media (max-width: 525px) {
  .average-rating-container {
    --length: 3em;
  }

  .average-rating-container .rating {
    font-size: 1.3em;
  }
}
