.login-signup-form {
  background-color: #fff;
  border-radius: 5px;
  padding: 2em 2em 0 2em;
  width: 100%;
  max-width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.login-signup-form-button {
  width: 100%;
}

.after-btn-text {
  margin-top: 1em;
  display: inline-block;
  width: 100%;
  text-align: center;
}
