header {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 10;
  height: 6em;
}

.navbar {
  padding: 1em 2em;
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}

nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav ul li:first-child {
  margin-right: 2em;
}

nav ul a {
  color: black;
  font-weight: bold;
  font-size: 1rem;
}

nav .logo {
  font-size: 2rem;
  font-weight: bold;
  color: black;
}
