.title > * {
  color: white;
  text-align: center;
  letter-spacing: 0.1em;
}

.title h3 {
  opacity: 0.7;
  font-weight: bold;
}

.title h2 {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 0.2em;
}

.title p {
  font-size: 1rem;
}

@media (max-width: 800px) {
  .title h2 {
    font-size: 3rem;
  }
}
