.search-form {
  width: 800px;
  margin: 2em auto;
  background-color: white;
  padding: 2em 2em 1em 2em;
  border-radius: 2px;
}

@media (max-width: 1100px) {
  .search-form {
    max-width: 700px;
  }
}

@media (max-width: 800px) {
  .search-form {
    width: unset;
    max-width: 700px;
    margin: 2em 1em;
  }
}
